/* 0. Reset ================================= */
@import 'reset';

@import 'variables';

@import 'fontfaces';

@import 'animations';

// @import 'hamburgers';

@font-face {
	font-family: "Caviar Dreams";
	src: url(../../dist/fonts/Caviar-Dreams/CaviarDreams.ttf);
}

@import url('https://fonts.googleapis.com/css?family=Lato|Manjari&display=swap');
$body-font: 'Manjari', sans-serif;
$heading-font: 'Lato', sans-serif;
$special-font: "Caviar Dreams";

/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
	-webkit-tap-highlight-color:  rgba(255, 255, 255, 0);
	user-select: none;
}

body {
	width: 100%;
	min-height: 100vh;
	font-family: $body-font;
	color: $dtu-black;
	background: $dtu-white;
	font-size: 18px;
	cursor: url(../../dist/img/logo-cursor.png), auto;

	h1, h2 {
		font-family: $heading-font;
	}

	h1 {
		font-size: 24px;

		span {
			font-weight: bold;
		}
	}

	h1, h2, h3 {
		line-height: 1.3;
		margin: 0 0 30px 0;
	}

	p {
		font-size: 18px;
	}
}

// /* 2. Content ================================= */


#app {
	width: 100%;
	min-height: 100vh;
	position: relative;

	header { //look into widows
	@include flex-support;
	width: 100%;
	padding: 1em;

		a {
			margin-left: auto;
			text-decoration: none;
			z-index: 1;

			.logo {
				background-position: center;
				background-size: cover;
				width: 65px;
				height: 68px;
				@include flex-support;
				@include align-items(flex-end);
			}
		}
	}

	.content {
		width: 100%;
		height: 100vh;

		.hero-content {
			position: relative;
			width: 100%;
			// max-width: 331px;
			// border: 1px solid $dtuBlack;
			padding: 1em;
			color: $dtu-black;
			text-align: center;

			// background: linear-gradient(43deg, #35b0ab, #634099);
			// background-size: 400% 400%;

			// -webkit-animation: dtu-gradient 15s ease infinite;
			// -moz-animation: dtu-gradient 15s ease infinite;
			// animation: dtu-gradient 15s ease infinite;

			.ash-icon {
				background-position: center;
				background-size: cover;
				height: 272px;
				margin: 0 0 30px 0;
			}

			.blockquote-carousel {
				font-family: $special-font;
				font-size: 50px;
				text-transform: uppercase;
				height: 50px;
				overflow: hidden;
				margin: 0 0 30px 0;

				div {
					@include flex-support;
					@include flex-direction(column);

					span {
						margin: 0 0 50px 0;
					}
				}
			}



			// @include bp-desktop {
			// 	max-width: 550px;
			// 	height: 55%;
			// 	align-self: flex-start;
			// 	margin: 0 0 0 40px;
			// }

			h3 {
				margin: 0 0 20px 0;
				font-weight: bold;
				text-align: center;
			}

			ul {
				@include inline-flex;
				@include flex-flow(row wrap);
				@include justify-content(center);
				@include align-items(center);
				max-width: 246px;
				
				@include bp-tablet {
					max-width: 280px;
				}
				
				li {
					margin: 7px 7px 20px 7px;

					svg {
						width: 47px;

						@include bp-tablet {
							width: 55px;
						}

						path, circle {
						fill: $dtu-white;
						}
					}
				}
			}
		}

		.scroll-prompt {
			@include flex-support;
			@include flex-direction(column);
			@include align-items(center);

			.scroll-arrow {
				background-repeat: no-repeat;
				width: 35px;
				height: 35px;
				margin: 0 0 10px 0;
			}
		}	
	}
}

#app::-webkit-scrollbar {
	width: 6px;
}

#app::-webkit-scrollbar-track {
	background: #ccc;
	border-radius: 6px;
}

#app::-webkit-scrollbar-thumb {
	width: 0.3em;
	background: $dtu-black;
	border-radius: 6px;
}

